import api from '../services/serviec.js'
import $ from 'jquery'
import numeral from 'numeral'

export default {
  name: 'main',
  data () {
    return {
      billAmount: 0,
      menu_datas: [],
      menu_list: [],
      language: 'กรุณาเลือกภาษา',
      languageID: 1,
      item_datas: [],
      item_lists: [],
      changlang: 'เปลี่ยนภาษา',
      title_count_order: 'จำนวนรายการ',
      unit_count_order: 'รายการ',
      btn_cancel: 'ยกเลิก',
      btn_or: 'หรือ',
      btn_payment: 'ชำระเงิน',
      detail_order_null: 'รายการสินค้าที่ต้องชำระ ...',
      Prices: [],
      md_itemID: 0,    
      md_titlename: '',
      md_choice_ID: 0,
      md_chioce_name: '',
      md_unit: '',
      md_cnt: 0,
      md_price: 0,
      choice: [],
      head_order: 'สินค้า',
      order_md_btn_close: 'ปิด',
      order_md_btn_add: 'เพิ่มรายการ',
      order_payment: [],
      payload: [],
      netPayment: 0,
      netAmount: 0,
      test_totalbill: 0,
      TPsuccess: '',
      b50: false,
      b100: false,
      b500: false,
      b1000: false
    }
  },
  methods: {
    fucnMoney (int) {
      return numeral(int).format('0,0')
    },
    select_lang(langID) {
      this.languageID = langID
      this.set_lang(langID)
      this.selectMenu(0)
      $('#lang').modal('hide')
    },
    set_lang(langID) {
      this.languageID = langID
      switch (langID) {
        case 1:
          this.sayLang('ภาษาไทย')
          this.language = 'กรุณาเลือกภาษา'
          this.changlang = 'เปลี่ยนภาษา'
          this.title_count_order = 'จำนวนรายการ'
          this.unit_count_order = 'รายการ'
          this.btn_cancel = 'ยกเลิก'
          this.btn_or = 'หรือ'
          this.btn_payment = 'ชำระเงิน'
          this.head_order = 'สินค้า'
          this.order_md_btn_close = 'ปิด'
          this.order_md_btn_add = 'เพิ่มรายการ'
          this.detail_order_null = 'รายการสินค้าที่ต้องชำระ ...'
          break
        case 2:
          this.sayLang('English language')
          this.language = 'Please select a language'
          this.changlang = 'Change Language'
          this.title_count_order = 'Number of items'
          this.unit_count_order = ''
          this.btn_cancel = 'Cancel'
          this.btn_or = 'or'
          this.btn_payment = 'Payment'
          this.head_order = 'Order'
          this.order_md_btn_close = 'Close'
          this.order_md_btn_add = 'Add Order'
          this.detail_order_null = 'Details of the order ...'
          break
        case 3:
          this.sayLang('中國')
          this.language = '选择你的语言'
          this.changlang = '更改語言'
          this.title_count_order = '商品編號'
          this.unit_count_order = ''
          this.btn_cancel = '取消'
          this.btn_or = '或'
          this.btn_payment = '付款'
          this.head_order = '訂購'
          this.order_md_btn_close = '關閉'
          this.order_md_btn_add = '添加訂單'
          this.detail_order_null = '訂單詳情 ...'
          break
      }
    },
    getApi(langID) {      
      $('#loading').modal({backdrop: 'static'}, 'show')
      api.getMenuAX((data) => {
        this.menu_datas = data
        this.languageID = langID
        this.selectMenu(0)        
        $('#loading').modal('hide')
      }, (response) => {
        console.log(JSON.stringify(response))
        $('#loading').modal('hide')
      })
    },
    selectMenu(ind) {
      if(this.menu_datas!=''){
        this.menu_list = []
        for(var i = 0; i < this.menu_datas.length; i++){
          if(this.menu_datas[i].lang_id == this.languageID){
            var menus = this.menu_datas[i]
            for(var r = 0; r < menus.menus.length; r++){
              if(r == ind){
                this.menu_list.push({ id: r, menu_code: menus.menus[r].Id, name: menus.menus[r].name, img: require('../assets/menu/' + menus.menus[r].image), isActive: true })
                this.getItemApi(menus.menus[r].Id)
              }else{
                this.menu_list.push({ id: r, menu_code: menus.menus[r].Id, name: menus.menus[r].name, img: require('../assets/menu/' + menus.menus[r].image), isActive: false })
              }
            }
          }
        }
      }else{
        this.getApi(this.languageID)
      }
    },
    sayLang(text) {
        switch (this.languageID) {
          case 1:
            var language = 'Thai Female'
            break
          case 2:
            var language = 'UK English Female'
            break
          case 3:
            var language = 'Chinese Female'
            break
        }
      setTimeout(function() {
          // responsiveVoice.setDefaultVoice(language)
          responsiveVoice.speak(text, language)
      }, 300)
    },
    getItemApi(menuID) {      
      $('#loading').modal({backdrop: 'static'}, 'show')
      api.getItemAX (menuID, (data) => {
        this.item_datas = data
        this.selectItemMenu(this.languageID)        
        $('#loading').modal('hide')
      }, (response) => {
        console.log(JSON.stringify(response))
        $('#loading').modal('hide')
      })
    },
    selectItemMenu(langID) {
      if (this.item_datas != '') {
        this.item_lists = []
        for(var i = 0; i < this.item_datas.length; i++){
          if (this.item_datas[i].lang_id == langID) {
            var items = this.item_datas[i]
            if (!items.items) {
              alert('สินค้านี้ไม่มีข้อมูล กรุณาตรวจสอบฐานข้อมูลระบบ')
              this.selectMenu(0)
            }else{
              for (var r = 0; r < items.items.length; r++) {
                try {
                  this.item_lists.push({item_code: items.items[r].Id, name: items.items[r].name, unit: items.items[r].unit, img: this.image(items.items[r].image), type_list: items.items[r].prices })
                } catch (e) { 
                  this.item_lists.push({item_code: items.items[r].Id, name: items.items[r].name, unit: items.items[r].unit, img: this.image('noItem.gif'), type_list: items.items[r].prices })
                  console.log(e) 
                }                
              }
            }
          }
        }
      } else {
        this.getItemApi(langID)
      }
    },
    image (name) {
      return require('../assets/item/'+name)
    },
    changeLang () {
      $('#lang').modal({backdrop: 'static'}, 'show')
    },
    openNodeitem (item) {
      $('#itemPrice').modal({backdrop: 'static'}, 'show')
      this.Prices = item.type_list
      this.md_titlename = item.name
      this.md_itemID = item.item_code
      this.md_unit = item.unit
      this.set_price(0)
    },
    CloseitemPrice () {
      this.md_itemID = 0
      this.md_titlename = ''
      this.md_cnt = 0
      this.choice_ID = 0
      this.choice_name = ''
      this.md_price = 0
      this.md_unit = ''
      this.choice = []
      $('#itemPrice').modal('hide')
    },
    set_price (ind) {
      var type_list = this.Prices
      for(var i = 0; i < this.Prices.length; i++){
        switch (this.languageID){
          case 1 : this.choice.push({id: type_list[i].id, name: type_list[i].name, price: type_list[i].price, img: require('../assets/logo.png'), isActive: false})
                  break
          case 2 : this.choice.push({id: type_list[i].id, name: type_list[i].name_en, price: type_list[i].price, img: require('../assets/logo.png'), isActive: false})
                  break
          case 3 : this.choice.push({id: type_list[i].id, name: type_list[i].name_cn, price: type_list[i].price, img: require('../assets/logo.png'), isActive: false})
                  break
        }
      }
      this.select_type(0)
    },
    select_type (inD) {
      for( var r = 0; r < this.choice.length; r++){
        if(r == inD){
          if(this.md_chioce_name == this.choice[r].name){            
            this.md_cnt += 1
            this.md_price = this.md_cnt * this.choice[r].price
          }else{
            this.md_cnt = 1
            this.md_price = this.md_cnt * this.choice[r].price
          }
          this.choice[r].isActive = true
          this.md_chioce_name = this.choice[r].name
          this.md_choice_ID = this.choice[r].id
        }else{
          this.choice[r].isActive = false
        }
      }
    },
    remove_QTY (inD) {
      for( var r = 0; r < this.choice.length; r++){
        if(r == inD){
          if(this.md_chioce_name == this.choice[r].name){            
            if(this.md_cnt != 1){
              this.md_cnt -= 1
              this.md_price = this.md_cnt * this.choice[r].price
            }else{
              this.md_cnt = 1
              this.md_price = this.md_cnt * this.choice[r].price
            }
          }else{
            this.md_cnt = 1
            this.md_price = this.md_cnt * this.choice[r].price
          }
          this.choice[r].isActive = true
          this.md_chioce_name = this.choice[r].name
        }else{
          this.choice[r].isActive = false
        }
      }
    },
    add_order () {
      var order = {
                   line: 0,
                   item_id: this.md_itemID,
                   item_name: this.md_titlename,
                   qty: this.md_cnt,
                   price_id: this.md_choice_ID,
                   price_name: this.md_chioce_name,
                   price: this.md_price,
                   unit: this.md_unit
                  }

      this.order_payment.push(order)
      this.calPayment()
      this.CloseitemPrice ()
    },
    clear_order () {
      this.order_payment = []
      this.billAmount = 0
    },
    calPayment () {
      var total = 0
      for(var i = 0; i < this.order_payment.length; i++){
        total += this.order_payment[i].price
        this.order_payment[i].line = i+1
      }
      this.billAmount = total
    },
    cancel_item_order (index) {
      if(this.order_payment.length != 0){
        this.order_payment.splice(index, 1)        
      }
    },
    send_payment () {
      $('#payment-onhand').modal({backdrop: 'static'}, 'show')
      switch (this.languageID) {
          case 1:
            var say = 'กรุณาชำระเงินตามจำนวนค่ะ'
            break
          case 2:
            var say = 'Please make payment'
            break
          case 3:
            var say = '請付款'
            break
        }
      this.sayLang(say)
      this.netAmount = this.billAmount
      this.payload = {
                      total: this.billAmount,
                      type: 'coffee',
                      sale_subs: this.order_payment
                      }
      this.test_payment(this.payload)
     
      // api.sendOrderAX (this.payload, 
      // (result) => {
      //   // this.$socket.sendObj({Device:"host",type:"request",command:"onhand"})    
      //   console.log(result)
      // }, (response) => {
      //   console.log(JSON.stringify(response))
      //   alert("API sale error"+ response)
      // })
    },
    websocket_onmessage() {
      this.$options.sockets.onmessage = (skResult) =>
      this.event_sk(JSON.parse(skResult.data))
    },
    event_sk (data) {
      // console.log(JSON.stringify(data))
      switch(data.command){
          case 'onhand' : this.bill_netAmount(data.data)
                          break
          case 'accepted_bill' : this.set_bil(data.data)
                          break
          case 'print'  : this.payment_sucess(data.data)
                          break
        }
    },
    bill_netAmount (payment) { 
      // console.log("billAmount "+this.payload.total)
      this.netAmount = this.payload.total - payment
      this.netPayment = payment
       switch (this.languageID) {
          case 1:
            var say = 'กรุณารับเงินทอน '+Math.abs(this.netAmount)+'บาท'
            break
          case 2:
            var say = 'Please accept '+Math.abs(this.netAmount)+'baht'
            break
          case 3:
            var say = '變化 '+Math.abs(this.netAmount)+'銖'
            break
        }
      
      if(this.netAmount <= 0){
        if(this.netAmount >= 0){          
          this.TPsuccess = 'กรุณารอรับใบเสร็จรับเงิน'
        }else{
          this.TPsuccess = 'กรุณารอรับใบเสร็จรับเงินและเงินทอน '+ Math.abs(this.netAmount) + ' บาท'
          this.sayLang(say)
        }
        this.show_cm()
        console.log('ทอนเงิน '+Math.abs(this.netAmount)+', netPayment '+this.netPayment)
      }else{
        console.log('เพิ่มเงิน '+this.netAmount+', netPayment '+this.netPayment)
        return false
      }
    },
    set_bil (data) {
      if(data.b50){this.b50=true}
      if(data.b100){this.b100=true}
      if(data.b500){this.b500=true}
      if(data.b1000){this.b1000=true}
    }, 
    show_cm () {
      $('#payment-onhand').modal('hide')
      $('#payment-success').modal({backdrop: 'static'}, 'show')
    },
    close_md_payment () {
      var cancel = {device:"host",type:"request",command:"cancel"}
      this.$socket.sendObj(cancel)
      $('#payment-onhand').modal('hide')
    },
    payment_sucess (data) {
      if(data=='success'){
        switch (this.languageID) {
          case 1:
            var say = 'ขอบคุณที่ใช้บริการค่ะ'
            break
          case 2:
            var say = 'Thank you'
            break
          case 3:
            var say = '感謝您使用該服務。'
            break
        }

        this.sayLang(say)       
        this.netAmount = 0
        this.netPayment = 0
        this.billAmount = 0
        this.payload = []
        this.order_payment = []
        this.selectMenu(0)
        $('#payment-success').modal('hide')
      }
    },
    test_payment (payload) {
      var payment = 20
      var r = Math.ceil(payload.total/payment)
      var netPayment = 0
      var withdrawal = payload.total
      var p = 0;
      var bb50 = false
      var bb100 = false
      var bb500 = false
      var bb1000 = false

      if(withdrawal>=50){ bb50 = true }
      if(withdrawal>=100){ bb100 = true }
      if(withdrawal>=500){ bb500 = true }
      if(withdrawal>=1000){ bb1000 = true }
      this.event_sk({command:"accepted_bill",data:{ b20:true, b50:bb50, b100:bb100, b500:bb500, b1000:bb1000}})

      var test = setInterval(function() {
        netPayment += payment
        if(withdrawal>=50){ bb50 = true }
        if(withdrawal>=100){ bb100 = true }
        if(withdrawal>=500){ bb500 = true }
        if(withdrawal>=1000){ bb1000 = true }
        this.event_sk({command:"accepted_bill",data:{ b20:true, b50:bb50, b100:bb100, b500:bb500, b1000:bb1000}})
        
        if(r==p){
          this.event_sk({command: 'print', data: 'success'})
          clearInterval(test)
        }else{          
          withdrawal -= netPayment
          this.event_sk({command: 'onhand', data: netPayment})
        }        
        p++;
      }.bind(this), 1000)
    },
    Soundclick () {
      var audio = document.getElementById("audio")
          audio.play()
    },
    stopSound () {
      var audio = document.getElementById("audio")
          audio.currentTime = 0
    }
  },
  mounted () {
    this.selectMenu(0)
    this.websocket_onmessage()
    console.log(localStorage.paybox_url)
    console.log('=========ready===========')
  }
}



